.App {
    text-align: center;
}
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    text-align: center;
}

.header {
    padding: 30px 15% 15px;
}

.headTitle {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.headTitle span {
    font-size: 1.5em;
}

.headTitle span:hover {
    background-color: rgba(131, 139, 132, 0.1);
}

.nav a {
    margin: 0;
    padding: 8px 16px;
    background-color: rgba(127, 170, 102, 0.562);
    color: #fff;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
    transition: background-color 0.3s;
}

.nav a:hover {
    background-color: #45a049;
}

hr {
    border: 0;
    border-top: 1px solid #ccc;
    margin: 20px 0;
}

.list {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}

.list-table {
    width: 80%;
    border-collapse: collapse;
    margin: auto;
}

.list-table td,
.list-table th {
    border-bottom: 1px solid #ddd;
    padding: 8px;
    text-align: center;
}

.listBody tr:hover {
    color: #000;
    font-weight: bold;
}

.list-table td button {
    padding: 5px 10px;
    background-color: rgba(151, 52, 52, 0.562);
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.list-table td button:hover {
    background-color: #e74c3c;
}

