/* App.css */

/* App.css */

.boardInsert {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: #f7f7f7;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    /* 그림자 추가 */
}

.boardInsert h2 {
    margin-bottom: 20px;
    color: #333;
}

.insert {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content,
.title,
.writer {
    margin-bottom: 16px;
}

.content th,
.title th,
.writer th {
    font-size: 18px;
    color: #333;
}

.title input {
    width: 600px;
    height: 30px;
}

.writer input {
    width: 600px;
    height: 30px;
}

.content input {
    width: 600px;
    height: 200px;
}

button {
    background-color: rgba(127, 170, 102, 0.562);
    color: #fff;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #45a049;
}

/* BoardDetail.css */

.boardDetail {
    text-align: center;
    margin-top: 50px;
}

.detail {
    width: 60%;
    margin: auto;
    border-collapse: collapse;
}

.detail tr {
    border-bottom: 1px solid #ddd;
}

.detail td,
.detail th {
    padding: 10px;
}

.detail th {
    background-color: #f2f2f2;
    width: 100px;
}

.detail td {
    text-align: left;
}

.detail td:first-child {
    font-weight: bold;
}

.detail tr+tr+tr {
    height: 500px;
}

.detail tr+tr+tr td {
    display: flex;
}

.updateBtn {
    text-align: center;
    /* 버튼 가운데 정렬을 위한 추가 스타일 */
    margin: 20px auto;
}

/* BoardUpdate.css */

.boardUpdate {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: #f7f7f7;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    /* 그림자 추가 */
}

.boardUpdate h2 {
    font-size: 1.5em;
    margin-bottom: 20px;
}

.update {
    margin-bottom: 20px;
}

.update label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
}

.update input {
    width: 100%;
    padding: 8px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

.content,
.title,
.writer {
    margin-bottom: 16px;
}

.content th,
.title th,
.writer th {
    font-size: 18px;
    color: #333;
}

.title input {
    width: 600px;
    height: 50px;
}

.writer input {
    width: 600px;
    height: 50px;
}

.content input {
  width: 600px;
  height: 200px;
}
